<template>
	<v-container>
		<v-row>
			<v-col>
				<v-card outlined>
					<v-card-title class="mb-2">   </v-card-title>
					<v-card-subtitle>  . </v-card-subtitle>
					<v-card-text>
						<ButtonCmp
							color="teal"
							width="100%"
							large
							class="mt-5"
							btnTxtClass="white--text"
						>
							 
						</ButtonCmp>
					</v-card-text>
				</v-card>
			</v-col>
		</v-row>
		<v-row>
			<v-col>
				<v-card outlined>
					<v-card-title class="mb-2">
						   
					</v-card-title>
					<v-card-subtitle>
						       
						      () 
						.
					</v-card-subtitle>

					<v-card-text>
						<ValidationObserver ref="observer2">
							<ValidationProvider
								v-slot="{ errors }"
								name=""
								:rules="{ required: true }"
							>
								<v-text-field
									v-model="name"
									label=""
									outlined
									solo
									dense
									:error-messages="errors"
								/>
							</ValidationProvider>
							<ValidationProvider
								v-slot="{ errors }"
								name=""
								:rules="{ required: true, numeric: true }"
							>
								<v-text-field
									v-model="phoneNumber"
									label=""
									solo
									outlined
									dense
									:error-messages="errors"
								/>
							</ValidationProvider>
							<ButtonCmp
								color="teal"
								width="100%"
								large
								class="mt-5"
								outlined
								@click="sendMsg"
							>
								
							</ButtonCmp>
						</ValidationObserver>
					</v-card-text>

					<template v-if="showAuthNumber">
						<v-divider />
						<v-card-text>
							<ValidationObserver ref="observer3">
								<ValidationProvider
									v-slot="{ errors }"
									name=""
									:rules="{ numeric: true, is: clickBtn && confirmation }"
								>
									<v-text-field
										v-model="authNumber"
										label=""
										outlined
										solo
										dense
										:error-messages="errors"
										@keydown="clickBtn = false"
									/>
								</ValidationProvider>
								<ButtonCmp
									color="teal"
									width="100%"
									large
									class="mt-5"
									btnTxtClass="white--text"
									@click="confirmAuthNumber"
								>
									
								</ButtonCmp>
							</ValidationObserver>
						</v-card-text>
					</template>
				</v-card>
			</v-col>
		</v-row>
	</v-container>
</template>

<script>
	import { ValidationProvider, ValidationObserver, extend } from 'vee-validate';
	import { email, required, numeric, is } from 'vee-validate/dist/rules';
	import ButtonCmp from '@/components/ButtonCmp.vue';
	// extend('max', () => {
	//   ...max
	//   console.log('hi');
	// });
	extend('email', {
		...email,
		message: '{_field_}  .',
	});
	extend('required', {
		...required,
		message: '{_field_}()  .',
	});
	extend('numeric', {
		...numeric,
		message: '{_field_} ',
	});
	extend('is', {
		...is,
	});

	export default {
		components: {
			ValidationProvider,
			ValidationObserver,
			ButtonCmp,
		},
		created() {},
		data() {
			return {
				name: '',
				phoneNumber: '',
				showAuthNumber: false,
				authNumber: null,
				confirmation: '',
				clickBtn: false,
			};
		},
		methods: {
			sendMsg() {
				this.$refs.observer2.validate();
				// ~
				const timer = setTimeout(() => {
					this.confirmation = '1234'; //  
					this.showAuthNumber = true;
					clearTimeout(timer);
				}, 2000);
			},
			confirmAuthNumber() {
				this.clickBtn = true;
				console.log(`  api  : ${this.authNumber}`);
			},
		},
	};
</script>

<style></style>
